body {
  margin: 0;
  font-family: Arial, sans-serif;
}
.App {
  display: flex;
  flex-direction: column;
  width: 100vw; /* Remove quotes */
  height: 100vh; /* Remove quotes */
}

.AppHeader {
  height: -20px;
  background-color: lightblue;
  color:aliceblue;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 20px; /* Removed unnecessary padding */
  width: 10%;
}

.course-form-container {
  margin: 20px auto;
  padding: 20px;
  width: 80%;
  background-color: #f0f2f5;
  border-radius: 8px;
}

/* Form container */
.form-container {
  display: flex;
  justify-content: space-between;
}

/* Left and Right containers inside the form */
.left,
.right {
  width: 48%; /* Left and Right containers take up 48% of the form container each */
}

/* Navbar styles */
.navbar {
  min-height: 50px; /* Minimum height of navbar */

  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.logo {
  font-size: 18px;
}

.menu {
  font-size: 14px;
}

/* Sidebar styles */
.sidebar {
  width: 80px; /* Fixed width for the sidebar */
  height: 100vh; /* Full height of the viewport */
  background-color: lightblue;
  padding-top: 50px; /* Padding for the top content */
  font-size: 16px;
}

.AppFooter {
  height: 50px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.15);
  background-color: black;
  color: white;
}
.first-navbar {
  height: 30px;
 
  display: flex;
  justify-content: space-between; /* Spacing between items */
  align-items: center;
  padding: 0 20px; /* Add padding */
}

.first-navbar .menu {
  display: flex;
  align-items: center;

}

.first-navbar .menu-item {
  margin-left: 20px; /* Add margin between items */
  
  cursor: pointer;
}

.first-navbar .menu-item:hover {
  color: #1890ff; /* Change color on hover */
}
/* app.css */

